.services {
	@include clr;
	&__cover {
		position: fixed;
		top: 0;
		left: 274px;
		right: 392px;
		bottom: 0;
		@include lg {
			top: 50px;
			left: 0; }
		@include md {
			left: 0;
			right: 340px; }
		@include sm {
			position: relative;
			top: 0;
			right: 0;
			height: 175px;
			width: 100%; } }
	&__wrap {
		float: right;
		position: relative;
		z-index: 2;
		min-height: 100vh;
		width: 392px;
		&:before {
			position: fixed;
			content: "";
			top: 0;
			right: 0;
			width: 392px;
			bottom: 0;
			z-index: -1;
			box-shadow: 0 22px 31px 0 rgba(0,0,0,0.35);
			@include md {
				width: 340px; }
			@include sm {
				display: none; } }
		@include lg {
			min-height: calc(100vh - 50px); }
		@include md {
			width: 340px; }
		@include sm {
			width: 100%;
			min-height: 0;
			pointer-events: none;
			touch-action: initial;
			overflow-anchor: initial;
			overflow: initial !important;
			background-color: $wildSand; } }
	&__info {
		padding: 25px 125px;
		margin-bottom: 110px;
		width: 100%;
		max-width: 700px;
		color: $white;
		background: $black;
		font-family: $book;
		@include f(25px, 400, 1.2);
		text-align: center;
		.services__cover & {
			@include xl {
				padding: 25px 60px;
				max-width: 500px; }
			@include lg {
				padding: 25px 50px;
				max-width: 400px;
				font-size: 20px; }
			@include sm {
				display: none; } }
		@include sm {
			padding: 36px 47px;
			margin: 0;
			max-width: none;
			background: rgba($black, 0.7);
			@include f(13px, null, 1.538); } } }
